/**
 *
 * Typography
 *
 */
.headline-base,
.sub-heading-base,
.text-primary {
  color: #970047;
}

a {
  color: #999;
}

a:hover {
  color: #999;
}

.modal-body {
  background-image: linear-gradient(180deg, #EFEFEF 25%, #D1D3D4 64%, #D1D3D4 100%);
}

.modal-footer {
  background-color: #d2d2d2;
}

.modal-header .close {
  color: #00629b;
  opacity: 1;
}

.modal-header .close:hover {
  color: #004872;
}

/* ----------------------------------------------------------
//  Actions
// ---------------------------------------------------------- */
.cta-banner,
.header-cta-banner {
  display: flex;
  justify-content: center;
}

.cta-banner {
  background-color: #00629b;
  color: #FFF;
}

.cta-banner a {
  color: inherit;
}

.header-cta-banner {
  background-color: #ebebeb;
  color: white;
}

.header-cta-banner a {
  color: #970047;
}

.btn {
  border-radius: 1.875em;
}

.btn-primary {
  background-color: #00629b;
  border-color: #00629b;
  color: #FFF;
}

.btn-primary:hover {
  background-color: transparent;
  border-color: #004872;
  color: #00629b;
}

.site-header .navbar-toggle {
  color: #970047;
}

.site-header .navbar-toggle a {
  color: inherit;
}

/*=========================================
=            Header CTA Banner            =
=========================================*/
.agent {
  background: #777;
  display: flex;
  justify-content: center;
  align-items: center;
}

.agent:after {
  border-color: rgba(136, 183, 213, 0);
  border-left-color: #777;
}

.agent-icon-fill {
  fill: #FFF;
}

/*=========================================
=            Header CTA Banner            =
=========================================*/
@media only screen and (max-width: 62em) {
  .navbar .flex-nav a {
    font-weight: normal;
  }
  .navbar .flex-nav li a {
    border-top: 1px solid #ebebeb;
  }
  .navbar .flex-nav li a:focus, .navbar .flex-nav li a:active {
    background-color: #00629b;
    border-top-color: #00629b;
    color: #FFF;
    font-weight: bold;
  }
}

.navbar-cart .badge {
  border-color: #00629b;
  color: #970047;
}

/**
 *
 * Forms.
 *
 */
.form-control {
  border-radius: 0;
}

.form-control[placeholder] {
  font-style: italic;
}

.email-signup {
  border-color: rgba(255, 255, 255, 0.5);
  text-align: center;
}

.nav-primary,
.footer-nav-primary {
  color: #999;
}

.navbar-brand {
  padding: 0 1em;
}

.order-status-nav {
  display: none;
}

.order-status-nav a {
  color: #999;
}

.order-status-nav a:hover {
  color: #999;
}

