/* ----------------------------------------------------------
//  Hero section
// ---------------------------------------------------------- */
.hero {
  background-image: linear-gradient(0deg, #D1D3D4 0%, #F6F6F6 64%, #D1D3D4 100%);
}

/* ----------------------------------------------------------
//  Hero with images
// ---------------------------------------------------------- */
.hero-image {
  background-color: #011840;
}

.hero-image-headline__title,
.hero-image-headline__subhead {
  color: #FFF;
}

.hero-image-cta {
  border-color: #ebebeb;
  border-color: rgba(255, 255, 255, 0.5);
}

.hero-image-cta__telgroup a {
  color: #FFF;
}

.hero-image-cta__title {
  color: #999;
}

.hero-image-cta__divider {
  color: #999;
}

/* ----------------------------------------------------------
//  Hero for Landing Pages
// ---------------------------------------------------------- */
.hero-product__price {
  background-color: #00629b;
  color: #FFF;
}

.page-type-phone .hero-product__price {
  background-color: #021D4B;
}

.hero-product__price:after {
  border-color: transparent;
  border-left-color: #00629b;
}

.page-type-phone .hero-product__price:after {
  border-left-color: #021D4B;
}

.hero-product__price .filter-price,
.hero-product__price .filter-label,
.hero-product__price .filter-label-inline {
  color: #FFF;
}

.hero-product__features h4 {
  color: #FFF;
}

.hero-product__features ul {
  color: #ebebeb;
}

.hero-promo__text {
  color: #FFF;
}

.hero-cta {
  color: #FFF;
}

.footer-navbar-light {
  background-color: #FFF;
}

.footer-navbar-light a {
  color: #00629b;
}

.footer-navbar-dark {
  background-color: #242424;
  color: #FFF;
}

.footer-navbar-dark a {
  color: #FFF;
}

.section-hr {
  border-bottom: 2px solid #ebebeb;
}

.section-hr-white {
  border-top: 2px solid #FFF;
}

/*=============================================
=            Frontpage            =
=============================================*/
@media only screen and (max-width: 62em) {
  .brand-value {
    border-bottom: 1px solid #ebebeb;
  }
}

.filter {
  color: #777;
  background-color: #FFF;
}

.filter-header {
  border-bottom: 2px solid #ebebeb;
}

.filter-price {
  color: #970047;
}

.filter-label,
.filter-label-inline {
